html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

.header {
  background-color: #FFFFFF;
}
.header .barra-background {
  background-color: #333333;
}
.header .barra {
  padding: 0.3rem 0 0.3rem 0;
  align-items: center;
}

.contenido-logo {
  padding: 1rem 0 1rem 0;
}
@media (min-width: 768px) {
  .contenido-logo {
    padding: 1rem 0 1rem 0;
  }
}
.contenido-logo a {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.contenido-logo a img {
  height: 50px;
  width: 180px;
}

@media (min-width: 768px) {
  .contenido-enlace nav {
    display: flex;
    justify-content: space-around;
  }
}
.contenido-enlace a {
  display: block;
  text-align: center;
  color: #FFFFFF;
  font-size: 2rem;
}
.contenido-enlace a:hover {
  color: #E08709;
}

.no-webp .contenido-banner {
  background-image: url("../img/banner.jpg");
}

.webp .contenido-banner {
  background-image: url("../img/banner.webp");
}

.contenido-banner {
  background-position: center center;
  background-size: cover;
  height: 10rem;
}
@media (min-width: 480px) {
  .contenido-banner {
    height: 20rem;
  }
}
@media (min-width: 768px) {
  .contenido-banner {
    height: 30rem;
  }
}
@media (min-width: 1024px) {
  .contenido-banner {
    height: 40rem;
  }
}

/* Slider inicio */
.carousel-elemento img {
  height: 30rem;
  width: 100%;
}

.carousel-contenedor {
  position: relative;
}

.carousel-anterior,
.carousel-siguiente {
  position: absolute;
  display: block;
  width: 3rem;
  height: 3rem;
  border: none;
  top: calc(50% - 3.5rem);
  cursor: pointer;
  line-height: 3rem;
  text-align: center;
  background: none;
  color: #fff;
}

.carousel-anterior {
  left: 0px;
}

.carousel-siguiente {
  right: 0px;
}

.carousel-lista {
  overflow: hidden;
}

.carousel-indicadores .glider-dot {
  background: rgb(218, 218, 218);
}

.carousel-indicadores .glider-dot:hover {
  opacity: 0.5;
}

.carousel-indicadores .glider-dot.active {
  background: rgb(243, 234, 188);
  opacity: 1;
}

/* Slider fin */
/* Barra cupones inicio */
.barra-cupones {
  display: flex;
  justify-content: space-around;
  column-gap: 2rem;
  padding: 2rem 0 2rem 0;
}
.barra-cupones div {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(134, 218, 198);
  height: 10rem;
  width: 29rem;
}

/* Barra cupones fin */
/* Barra anuncio inicio */
.barra-anuncio {
  padding: 2rem 0 2rem 0;
}
.barra-anuncio div {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #E08709;
  height: 10rem;
}

/* Barra anuncio fin */
.seccion-ofertas {
  background-color: #FFFFFF;
}
.seccion-ofertas h1 {
  margin: 0 0 0 5rem;
  font-size: 7rem;
  line-height: 8rem;
  text-transform: uppercase;
  color: #E08709;
}
@media (min-width: 768px) {
  .seccion-ofertas h1 {
    line-height: 10rem;
  }
}

.contenedor-anuncios {
  background-color: #eeeeee;
  border-radius: 1% 1% 0 0;
  padding: 2rem;
}
@media (min-width: 480px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}

.anuncio {
  padding: 1rem;
  background-color: #FFFFFF;
  border-radius: 5%;
  margin-bottom: 1rem;
}
.anuncio:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .anuncio {
    margin-bottom: 0;
  }
}
.anuncio picture {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #FFFFFF;
  border-radius: 5% 5% 0 0;
}

.contenido-anuncio {
  margin-top: 1rem;
  border-top: 2px solid #e1e1e1;
}
.contenido-anuncio p {
  font-size: 1.7rem;
  text-align: center;
  font-weight: 400;
  color: #17277e;
}

.contenido-boton {
  padding: 0 1rem 1rem 1rem;
}
.contenido-boton .boton {
  background-color: #0e4eff;
  color: #FFFFFF;
  padding: 1rem;
  border: none;
}
@media (min-width: 768px) {
  .contenido-boton {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
  }
}
.contenido-boton a {
  display: block;
  text-align: center;
  font-size: 1.4rem;
}
.contenido-boton a:hover {
  background-color: #003ada;
}

.seccion-info {
  display: flex;
  flex-direction: column;
  background-color: #333333;
}

li, legend, label {
  color: #FFFFFF;
}

legend {
  text-align: center;
  text-transform: uppercase;
}

.contenido-info {
  display: flex;
  flex-direction: column;
  row-gap: 10rem;
  align-items: center;
  padding: 5rem;
}
.contenido-info ul {
  list-style: none;
  padding: 0;
}
@media (min-width: 768px) {
  .contenido-info {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    row-gap: auto;
  }
}

.menu-lista ul {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .menu-lista ul {
    align-items: flex-start;
  }
}

.seccion-redes-sociales {
  background-color: #1a1a1a;
  padding: 2rem 0;
}

.iconos-redes-sociales {
  display: flex;
  flex-direction: row;
  justify-content: center;
  column-gap: 3rem;
  align-items: center;
  list-style: none;
}
.iconos-redes-sociales .icono {
  height: 3rem;
}
@media (min-width: 768px) {
  .iconos-redes-sociales {
    justify-content: flex-start;
  }
}

.formulario fieldset {
  display: flex;
  flex-direction: column;
  min-width: 30rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .formulario fieldset {
    width: 40rem;
  }
}
.formulario .contenido-form {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  grid-template-columns: 20% auto;
}

.boton-formulario {
  display: flex;
  flex-direction: column;
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .boton-formulario {
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
}
.boton-formulario .boton {
  background-color: #E08709;
  color: #FFFFFF;
  padding: 1rem;
  border: none;
}
.boton-formulario input {
  text-align: center;
  font-size: 1.4rem;
  width: auto;
}
.boton-formulario input:hover {
  background-color: #af6907;
}
@media (min-width: 768px) {
  .boton-formulario input {
    width: 20rem;
  }
}

.footer {
  background-color: #000000;
  padding: 2rem 0;
}
.footer .contenedor-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer .contenedor-footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.contenido-footer-1 p,
.contenido-footer-2 p {
  font-size: 1rem;
  color: #FFFFFF;
}

.contenido-footer-1 p {
  margin-bottom: 0;
  margin-top: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .contenido-footer-1 p {
    text-align: left;
  }
}